    .endpoint-summary{
        background-color: #FFF;
        box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
    }
    .graph-row{
        margin: 2% 0;
        .summary-key,
        .summary-value{
            padding: 2%;
        }
        .summary-section + .summary-section{
            .summary-key,
            .summary-value{
                border-left: solid 1px rgba(0,0,0,0.1);
            }
        }
        .summary-key{
            background-color: #615E8A;
            color: #FFF;
            font-weight: bold;
        }
    }
</style>